h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

.infoWrap {
  margin-bottom: 20px;
  display: flex;
}

.textWrap {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.poster {
  margin-right: 20px;
}

.title {
  margin: 0;
}

.genresList {
  list-style: none;
  display: flex;
  gap: 20px;
  padding: 0;
  margin: 0;
}

.linkBtn {
  padding: 2px;
  margin-bottom: 5px;
  background-color: lightgray;
  border: 2px solid gray;
  width: 60px;
}
